/* typography & colors */
html, body {
  color: #5b4848;
}
h1, h2, h3, h4, h5, h6 {
  color: #A00631;
}
a, a:visited, a:focus, a:hover {
  color: #8c9465;
}
header {
  background: #F9F1E6;
}
body > header {
  border-right: 2px solid #5b4848;
}
header > img {
  background: white;
}
ul > li:before,
ol > li:before {
  color: #A00631;
}



