
body {
  font-family: Hind Light;
}
h1, h2, h3, h4, h5, h6 {
  font-family: Crimson Text;
}
ul, ol {
  list-style: none;
  counter-reset: li-counter;
  padding: 0;
  margin: 0;
}
li {
  padding-left: 1em;
}
ul > li:before,
ol > li:before {
  padding-right: .5em;
  margin-left: -1em;
}
ul > li:before {
  content: "♦";
}
ol > li:before {
  content: counter(li-counter, upper-roman);
  counter-increment: li-counter;
  font-weight: bold;
  font-family: Crimson Text;
}
p.lead {
  font-size: 1.4em;
  font-style: italic;
}
::selection {
  background-color: #A00631;
  color: #FFF;
}



