@import 'reset.scss';
@import 'grid.scss';
@import 'layout.scss';
@import 'typography.scss';
@import 'colors.scss';
@import '../node_modules/css-modal/modal';

/* @import 'variables.scss';*/
/* @import '../node_modules/bootstrap/scss/bootstrap.scss'*/

* {
  -webkit-transition: 0.3s ease all;
  transition: 0.3s ease all;
}
:last-child {
  margin-bottom: 0;
}

/* backgrounds and images */
body {
  background: url("/images/A_orclair.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 5em 0;
}


/* menus */
a.menu-item,
a.menu-item:visited,
a.menu-item:focus,
.menu-item {
  color: white;
  background: #5b4848;
  color: #5b4848;
  background: white;
}
.menu-item > a,
.menu-item > a:visited,
.menu-item > a:focus {
  color: inherit;
  text-decoration: inherit;
}
a.menu-item:hover,
.menu-item:hover,
.menu-item > a:hover {
  background-color: #787e56; /* #8c9465 */
  color: #eecc84; /* #D3A15A */
  text-decoration: inherit;
}
.menu-item {
  display: block;
  margin: 0 -2px 2px 0;
  padding: 0 .7em;
  border-right: 2px solid #5b4848;
}
.menu-item:last-of-type {
  margin-bottom: 0;
}
.menu-item {
  line-height: 3em;
  vertical-align: text-bottom;
  font-size: .7em;
  font-weight: bold;
  font-family: Omnia;
  white-space: nowrap;
  text-decoration: none;
  cursor: pointer;
}
.menu-item:hover {
  border-right-color: white;
}
.menu-item + .submenu {
  display: none;
}


/* tabbed nav */
.tabs {
  margin: 0 -2em;
  padding-top: 1em;
}
.tabs > input,
.tabs > div {
  display: none;
  margin: 0 2em;
}
.tabs > .titles {
  /*display: block;*/
  width: 100%;
  margin: 0;
  padding: 0 2em;
  background: #8C9465;
}
.tabs > .titles > .wrapper {
  display: table-row;
}
.tabs > .titles label {
  display: table-cell;
  padding: .5em 1em;

  background: #8C9465;
  color: white;
  font-family: Omnia;
  font-weight: bold;
  font-size: 1.3em;
}
#tab-1:checked ~ .tab-1 {
  display: block;
}
#tab-2:checked ~ .tab-2 {
  display: block;
}
#tab-1:checked ~ .titles [for=tab-1] {
  background: #D3A15A;
}
#tab-2:checked ~ .titles [for=tab-2] {
  background: #D3A15A;
}


/* cards */
/*@import "~susy/sass/susy";*/

.card {
}
a.card {
  display: block;
  background: #787e56;
  color: #eecc84;
  font-family: Crimson Text;
  text-decoration: none;
  text-align: center;
}
a.card:hover > figure > img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.card > figure {
  margin: 0;
  position: relative;
  overflow: hidden;
}
.card > figure > img {
  width: 100%;
  display: block;
}
.card > figure > figcaption {
  padding: 1em;
  /*position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: #787e56dd;/**/
}


/* more specific stuff */

.beta-target-enabler:not(:target) + * .beta {
  display: none;
}

.photos-CA {
  overflow: auto; /* clearfix */
}
.photos-CA:after { /* another clearfix method which sometimes avoids scrollbars */
  content:"";
  display:block;
  clear:both;
}
.photos-CA > img {
  float: left;
  width: 10%;
}

.activities:after { /* another clearfix method which sometimes avoids scrollbars */
  content:"";
  display:block;
  clear:both;
}
.activity {
  width: 15em;
  float: left;
  margin-right: 1em;
}

.activity-questions {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.question {
  display: block;
  width: 30%;
  border: 1px solid #787e56;
  margin-bottom: 1em;
  background: white;
  text-decoration: none;
}
.question {
}
.question .header{
  padding: .5em .5em 0 .5em;
}
.question .header > img{
  float: left;
  height: 3em;
  width: auto;
  margin-right: .5em;
}
.question .header > h3{
  margin: 0;
  line-height: 3em;
}
.question .header:after{
  content: "";
  display: block;
  clear: both;
}
.question p{
  padding: 0 1em;
}
.question .citymapper{
  display: block;
  background: #37AB2F;
  line-height: 3em;
  padding-left: 1em;
}
.question .citymapper img{
  display: inline-block;
  float: right;
  height: 3em;
  width: auto;
}
.question .citymapper:link,.question .citymapper:visited{
  color: white;
  text-decoration: none;
}
.question .citymapper:after{
  content: "";
  display: block;
  clear: both;
}

section {
  border: 1px solid #5b4848;
  padding: .5em;
  margin-top: 1em;
  background: white
}

section > .header-image{
  display: block;
  float: left;
  margin-right: .5em;
  height: 5em;
  width: 6.9em;
}

section > .header-image img:first-child{
  display: block;
  height: 100%;
  width: auto;
  margin: auto;
}

section h3{
  line-height: 1.5em;
  padding-top: .3em;
  padding-bottom: .5em;
  margin: 0;
}

section :last-child{
  margin-bottom: 0;
}

section .citymapper{
  display: block;
  background: #37AB2F;
  line-height: 3em;
  padding-left: 1em;
}

section .citymapper img{
  display: inline-block;
  float: right;
  height: 3em;
  width: auto;
}

section .citymapper:link,section .citymapper:visited{
  color: white;
  text-decoration: none;
}

section .citymapper:hover{
  font-weight: bold;
}

section .citymapper:after{
  content: "";
  display: block;
  clear: both;
}
section:after {
  content: "";
  display: block;
  clear: both;
}

.google-map {
  display: block;
  width: 100%;
  height: 0;
  padding-bottom: 73.33333%;
  position: relative;
  clear: both;
  overflow: hidden
}

.google-map img{
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.google-map img:hover{
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}


/* cheatcodes */
:target {
  display: block!important;
}
:target ~ .default {
  display:none;
}

.modal--show {
  background: none;
}
.is-active.modal--fade, .is-active.modal--show, .modal--fade:target, .modal--show:target {
  margin-top: 0;
}
.modal--fade .modal-close:before, .modal--show .modal-close:before {
  opacity: .5;
}
.modal--fade .modal-inner,
.modal--show .modal-inner {
  margin-left: -25em;
  width: 50em;
  opacity: 1;
}
.modal--fade .modal-close:after,
.modal--show .modal-close:after {
  margin-right: -25em;
  display: none;
}
.modal--fade header,
.modal--show header {
  padding: .4em .7em .3em
}
.modal--fade header h1, .modal--show header h1{
  color: #5b4848;
}



